@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap);
body {
  margin: 0;
  font-family:'Josefin Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #3dc0b0;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.4)
    
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 90%;
    max-width: 600px;
    margin: 0 auto;
}

a {
    width: 25%;
    text-decoration: none;
    color: #fff;
    height: 50px;
    line-height: 50px;
}

a:hover {
    box-sizing: border-box;
    border-bottom: 5px solid;
}
#home {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* height: 100vh; */
    padding-top: 50px;
    overflow: auto;
}

#home img {
    -webkit-filter: grayscale(60%);
            filter: grayscale(60%);
    z-index: -1;
    width: 150px;
    border-radius: 100%;
    box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.60);
}

#home > p {
    font-size: 1.5rem;
}

h1, p {
    text-transform: uppercase;
}

h1 {
    font-weight: 100;
    font-size: 8vw;
    letter-spacing: 0.1rem;
    margin: 2rem 0;
}

.intro-container {
   width: 90%;
   flex-grow: 0.5;
   display: flex;
   flex-direction: column;
   justify-content: center;
}
.intro-container > p {
    text-transform: none;
    text-transform: initial;
    /* padding: 30% 10%; */
    line-height: 2rem;
    text-align: left;
    font-size: 1.4rem;
}

@media screen and (min-width: 1000px) {
    h1 {
        font-size: 70px;
    }

    .intro-container {
        width: 60%;
    }
}
.card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 4px;
    background-color: #3dc0b0;
    width: 80%;
    margin: 15px auto;
    padding: 5px 10px;
    color: #fff;
    box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.4);
}


.project-links {
    display: flex;
    justify-content: space-around;
    align-items: center;

}

.project-links > a {
    flex-grow: 1
}

.title {
    font-weight: bold;
}

@media screen and (min-width: 600px) {
    .card {
        width: 40%;
    }

}
@media screen and (min-width: 950px) {
    .card {
        width: 30%;
    }

}
@media screen and (min-width: 1100px) {
    .card {
        width: 20%;
    }

}
#projects {
    /* height: calc(100vh - 50px); */
    /* overflow */
    padding-top: 50px;
}

#projects > h1 {
    /* margin-top: 5px; */
}

.cards-container {
    display: flex;
    flex-wrap: wrap;
    max-width: 1300px;
    margin: 0 auto;
}

footer {
    margin-top: 50px;
    border-radius: 100% 100% 0px 0px;
    background-color: #3dc0b0;
    box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.4);
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.icons-container > * {
  margin: 5px 10px;
}

.App-link {
  color: #09d3ac;
}

